@import "_variables";

//reset

html, body, div, span, applet, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,b, u, i, center,
dl, dt, dd, ol, ul, li,fieldset, form, label, legend,table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  // font: inherit
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

//----

a {
  text-decoration: none;
  &:hover {} }


//----

* {
  font-family: "Lato", 'Noto Sans TC','微軟正黑體';
  background-size: cover;
  background-position: center center;
  box-sizing: border-box;
  position: relative;
  vertical-align: top;
  background-repeat: no-repeat;
  font-weight: 400;


 }  // font-family: '新黑體 R'


//slick

.slick-track,.slick-list,.slick-slider {
  height: 100%; }

.slick-dotted.slick-slider {
  margin: 0; }

.slick-slide:focus {
  outline: none; }

.fa.fa-angle-left,.fa.fa-angle-right {
  @include ab_center;
  z-index: 105;
  color: white;
  font-size: grid_sz(0,1.2);
  top: calc(50% - #{grid_sz(0,1)/2});
  cursor: pointer; }
.fa.fa-angle-left {
  left: 50px; }
.fa.fa-angle-right {
  left: initial;
  right: 50px; }


ul.slick-dots {
  bottom: 20px;
  li {
    margin: 0;
    padding: 0px 13px;
    button {
      &:before {
        @include trans;
        content: "";
        @include size(8px);
        border-radius: 50%;
        background-color: rgba(white,0.8);
        border: solid 1px rgba(white,1); } } } }


//tag size
h1 {
  @include paragraph_size_leading(48px,83px,2px); }
h2 {
  @include paragraph_size_leading(34px,50px,2px); }
h3 {
  @include paragraph_size_leading(26px,36px,2px); }
h4 {
  @include paragraph_size_leading(21px,31px,2px); }
h5 {
  @include paragraph_size_leading(17px,28px,2px); }
h6 {
  @include paragraph_size_leading(16px,27px,2px); }
p {
  @include paragraph_size_leading(16px,27px,3px);
  color: $color_grey;
  text-align: justify !important;
  transition: 0s; }


@include range_width(0px,800px) {
  h1 {
    @include paragraph_size_leading(36px,76px,2px); }
  h2 {
    @include paragraph_size_leading(30px,45px,2px); }
  h3 {
    @include paragraph_size_leading(24px,36px,2px); }
  h4 {
    @include paragraph_size_leading(20px,32px,2px); }
  p {
    @include paragraph_size_leading(16px,27px,3px);
    color: $color_grey;
    text-align: justify !important;
    transition: 0s; } }

hr {
  margin-top: grid_sz(0,2);
  margin-bottom: grid_sz(0,2);
  width: 100%;
  border: none;
  border-bottom: solid 1px #eee; }


body {
  @include paragraph_size_leading(16px,27px,3px);

  font-weight: 400; }

html,body {
  // min-width: 1280px
  // min-height: 900px
  width: 100%;
  overflow-x: hidden; }

.big_font {
  &,p,.question_list {
    @include paragraph_size_leading(18px,32px,3px); } }


h1,h2,h3,h4,h5,h6 {
  color: $color_grey_dark;
  font-weight: 300;
 }  // min-width: 1600px
h3,h4,h5,h6 {
  font-weight: 400; }

.color_theme {
  color: $color_theme; }

.color_white {
  color: #fff; }


.timeline {
  width: 100%;
  height: 2px;
  background-color: #ddd;
  position: absolute;
  bottom: 0px;
  z-index: 90;
  .value {
    width: 50%;
    height: 100%;
    background-color: #888; } }

//----


section {
  // min-height: 100vh
  background-size: cover;
  background-position: 60% center;
  .section_title {
    margin-bottom: grid_sz(0,3);
    margin-top: grid_sz(0,-3);
    @include trans;
    &.initial {
      opacity: 0; } }
  .section_para {
    @include trans(1s,0.2s);
    &.initial {
      opacity: 0; } }

  .slide_bg_set {
    width: 100%;
    height: 100%; } }



//---

.container {
  max-width: grid_sz(12,13);
  margin: auto;
  width: 100%;
  height: 100%;


  &.flex {
    display: flex;
    flex-direction: row;
    align-items: center;

    @include range_width(0px,800px) {
      height: auto; } }

  &.center {
    justify-content: center; }

  &.wrap {
    flex-wrap: wrap; }

  &.full {
    max-width: initial; }

  &.column {
    flex-direction: column; }

  &.row {
    flex-direction: row; }

  &.top_out {
    background-color: $color_white;
    margin-top: grid_sz(0,-3);
    @include grid_sz_mx(12,15);
    padding: grid_sz(0,1);
    flex-wrap: wrap;
    padding-top: grid_sz(0,3);
    align-items: flex-start; }

  .col_left,.col_right {
    width: 50%;
    padding: grid_sz(0,1,10px);
    text-align: center;
    height: 100%;
    @include flex_center;
    flex-direction: column;
    p {
      text-align: left; } }

  @include rwd_sm {
    &.flex {
      flex-direction: column; }
    .col_left , .col_right {
      width: 100%;
      min-height: 50vh; } }
  .col_hero {
    width: grid_sz(6,5);
    text-align: center;

    @include range_width(0px,800px) {
      width: 100%;
      padding-left: grid_sz(0,1);
      padding-right: grid_sz(0,1); } } }


.bg_parallax {
  background-size: auto 100%; }
.bg_parallax_about {
  background-size: 100% auto; }
.cover_image {
  background-color: $color_theme; }

//----------

.btn {
  border-radius: 2px;
  letter-spacing: 2px;
  padding: 10px grid_sz(0,1);
  font-weight: 300;
  font-size: 15px;
  letter-spacing: 4px;
  text-decoration: none !important;
  cursor: pointer;
  display: inline-block;

  @include trans; }

.btn-primary {
  border: none;
  background-color: $color_theme;
  color: $color_white;
  &:hover {
    background-color: lighten($color_theme,15);
    color: $color_white; } }

.btn-primary-lighter {
  border: none;
  background-color: lighten($color_theme,7);
  color: $color_white;
  &:hover {
    background-color:  lighten($color_theme,15);
    color: $color_white;
    border-color:  lighten($color_theme,7); } }

.btn-transparent {
  background-color: transparent;
  border: solid 1px $color_white;
  color: $color_white;
  &:hover {
    background-color: $color_white;
    color: $color_theme;
    border-color: $color_white; } }

@mixin cover_opacity($color: black) {
  display: block;
  content: "";
  position: absolute;
  @include size(100%,100%);
  left: 0;
  top: 0;
  background-color: $color; }

//----------------
// canvas

canvas.wave {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%; }



//----------------

//用線分隔的導覽
.nav_line_split {
  list-style: none;
  padding: 0;
  li {
    display: inline-block;
    padding: 0px $gutter_width;
    // margin-bottom: $gutter_width*2
    border-right: solid 1px $color_theme;
    cursor: pointer;
    @include trans;
    &:hover {
      color: $color_theme; }
    &:last-child {
      border-right: none; }
    &.active {
      color: $color_theme; } } }

a {
  color: $color_theme; }

.default_bg {
  background-image: url("../img/homepage/Home7.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  &:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(white,0.4); } }


//nav
//--------------------------

nav {
  $nav_height: grid_sz(0,3);
  @include size(100%,$nav_height);


  position: fixed;
  top: 0;
  font-size: 17px;
  @include colors_bg_text(white,$color_grey_dark);
  padding-top: grid_sz(0,1)/2;
  border-top: solid 3px $color_theme;
  z-index: 200;
  @include trans;

  * {
    color: $color_grey;
    text-decoration: none;
    font-weight: 400; }

  .row {
    display: flex;
    width: 100%;
    justify-content: space-between; }

  //---
  .navbar-header {
    width: grid_sz(3,2);
    @include range_width(0px,800px) {
      padding-left: grid_sz(0,0.8); }
    img {
      height: 50px;
      @include trans; }
    @include rwd_md {
      width: grid_sz(2,2); } }

  .nav-leftpart {
    position: relative;
    vertical-align: middle;
    > * {
      display: inline-block;
      vertical-align: top; } }

  .navbar-search {
    .highlight {
      // background-color: red
      // color: white
      padding: 2px 1px 4px 1px;
      border-bottom: solid 2px $color_theme; } }

  .navbar-search-input {
    position: absolute;
    top: grid_sz(0,0.5);
    width: 100%;
    max-width: grid_sz(7,6);
    pointer-events: none;
    opacity: 0;
    @include trans;
    @include rwd_sm {
      width: 100vw;
      left: 0px;
      // height: grid_sz(0,4)
      background-color: $color_white;
      // box-shadow: 0px 0px 0px 20px white
      top: grid_sz(0,0.25);
      margin-bottom: grid_sz(0,0.25); }
    input {
      width: calc(100% - #{grid_sz(0,2)});
      // box-shadow: 0px 0px 0px 5px white
      border: none;
      // border: solid 1px #ccc
      padding: 12px 12px;
      // border-radius: 30px
      padding-left: 50px;
      letter-spacing: 1px;
      &::placeholder {
        opacity: 0.4; }
      @include rwd_sm {
        margin-left: grid_sz(0,1); } }
    i.fa-search {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 20;
      left: 15px;
      @include rwd_sm {
        left: 39px; } }
    i.fa-times {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 20;
      right: 15px;
      left: initial;
      cursor: pointer;
      @include rwd_sm {
        right: 39px; } } }



  .navbar-left {
    margin-left: grid_sz(0,1);
    // width: grid_sz(7,6)
    width: auto; }

  .navbar-right {
    width: grid_sz(2,2);
    display: flex;
    justify-content: flex-end;
    align-items: space-between; }

  .navbar-left,.navbar-right {
    margin-top: grid_sz(0,1)/2;
    @include trans; }

  //---



  .navbar-nav li {
    list-style: none;
    display: inline-block;
    margin-right: grid_sz(0,1);

    & a.router-link-active {
      @include colors_bg_text(transparent,$color_theme);
      border-bottom: solid 1px $color_theme;
      &:hover {
        color: $color_theme;
        background-color: transparent; } }

    & > a {
      padding-bottom: 6px;
      border-bottom: solid 1px transparent;
      @include trans; }


    & ul.subnav {
      width: 100vw;
      position: fixed;
      @include pos(0,$nav_height);
      opacity: 0;
      overflow: hidden;
      max-height: 0px;
      height: 0px;
      transition: 0.5s 0.1s, top 0.5s 0s;
      text-align: center;
      background-color: rgba($color_theme,0.9);
      li {
        margin: 0;
        border-bottom: none;
        cursor: pointer;

        @include trans;
        &:nth-child(2n) {
          margin-left: grid_sz(0,1);
          margin-right: grid_sz(0,1); }
        a {
          text-decoration: none;
          border-bottom: transparent;
          color: rgba(white,0.8);
          @include trans; }
        &:hover a {
          color: rgba(white,0.9); } }
      .container {
        justify-content: flex-start; } }

    &:hover {
      a {
        border-bottom: solid 1px $color_theme; }
      .subnav {
        max-height: 200px;
        height: grid_sz(0,3);
        opacity: 1;
        z-index: 100;
        padding: grid_sz(0,1) 0px;
        a.router-link-active {
          color: white; } } } }

  .func_burger,.func_search,.func_lang {
    cursor: pointer;
    &,i,span {
      @include trans; }
    &:hover {
      &,i,span {
        color: $color_theme; } } }



  @include range_width(0px,1080px) {

    // .navbar-nav
    //   .func_lang,.func_search
    //     display: none
    .row {
      justify-content: space-between; }
    .nav-right {
      justify-content: flex-end; } }


  @include range_width(0px,1100px) {
    .navbar-nav.navbar-left {
      display: none; } }


  @include range_width(0px,800px) {
    width: 100%;
    max-width: 100%; }


  @include range_width(0px,400px) {
    .navbar-nav {
      .func_lang,.func_size {
        display: none; } } }

  //---- when at top of the page
  @include range_width(800px,3000px) {
    &.at_top {
      $nav_height_at_top: grid_sz(0,5);
      height: $nav_height_at_top;
      padding: grid_sz(0,1);

      .navbar-search-input {
        top: grid_sz(0,0.5); }

      .navbar-header {
        width: grid_sz(3,2);
        img {
          height: 65px; } }

      .navbar-left,.navbar-right {
        margin-top: grid_sz(0,1); }


      .navbar-nav li {
        & ul.subnav {
          @include pos(0,$nav_height_at_top); } } } }
  &.search {
    .navbar-search-input {
      pointer-events: initial;
      opacity: 1; }
    .navbar-left {
      pointer-events: none;
      opacity: 0; } }



  .func_size {
    padding-top: 3px;
    filter: saturate(100%) brightness(1) contrast(1);
    @include trans;
    &:hover {
      filter: saturate(0%) brightness(0.7) contrast(5); }
    .icon_big,.icon_small {
      cursor: pointer; } } }




.slide_bullet {
  list-style: none;
  position: fixed;
  right: 40px;
  top: 50%;
  transform: translate(-50%,0px);
  z-index: 500;
  li {
    display: block;
    @include size(9px);
    border-color: $color_grey_light;
    border-radius: 50%;
    margin-bottom: 16px;
    cursor: pointer;
    @include trans;
    position: relative;
    &:after {
      content: "";
      display: block;
      border-radius: 50%;
      @include ab_center;
      @include size(6px);
      background-color: darken($color_grey,9);
      @include trans; }

    &:hover {
      border-color: $color_theme;
      &:after {
        background-color: $color_theme;
        @include size(12px); } }
    &.active {
      background-color: $color_theme;
      &:after {
        background-color: $color_theme;
        @include size(12px); } } }
  @include range_width(0px,800px) {
     display: none; } }

//----------------------------------------------
footer {
  background-color: $color_theme;
  color: $color_white;
  height: 61px;
  font-weight: 300;


  .info {
    display: inline-block;
    margin: 0;
    font-size: 14px;
    opacity: 0.9;
    letter-spacing: 2px;
    width: grid_sz(6,5); }

  ul.footer_nav {
    display: block;
    li {
      display: inline-block;
      letter-spacing: 3px;

      padding: 0px 15px;
      border-right: solid 1px $color_white;
      a {
        @include trans; }
      &:hover a {
        color: $color_white; }
      &:last-child {
        border-right: none; } }

    a {
      color: rgba($color_white,0.7);
      text-decoration: none;
      font-size: 17px; } }


  @include rwd_md {
    height: auto; }

  .container.flex {
    @include flex_split("lr");
    align-items: center;




    @include range_width(0px,930px) {
      flex-direction: column;
      padding: grid_sz(0,1) 0px;
      .info {
        margin-bottom: grid_sz(0,1); } }

    @include range_width(0px,600px) {
      .info {
        margin-bottom: 0px; } }

    @include rwd_md {
      ul.footer_nav {
        display: none; }
      .info {
        width: 100%;
        text-align: center; } } } }




//-----------------------------------------------
section.section_solution {
  background: url("../img/homepage/Home7.jpg") 60% bottom;
  background-size: cover;
  min-height: 100vh;
  position: relative;
  padding-top: grid_sz(0,2);
  padding-bottom: grid_sz(0,2);
  text-align: center;

  .container.flex {
    min-height: grid_sz(0,24);
    height: auto;
    padding: 0px grid_sz(0,1);
    justify-content: center; }
  .slogan {
    margin-top: grid_sz(0,1);
    margin-bottom: $gutter_width;
    color: $color_grey_light;
    font-weight: 400; }
  &:before {
    @include cover_opacity(rgba(white,0.6)); }

  h3.section_title {
    margin-top: grid_sz(0,2);
    margin-bottom: grid_sz(0,2);
    color: $color_grey_dark;
    font-size: 24px; }
  .solution_box_row {
    margin-top: $gutter_width*1;
    // margin-bottom: $gutter_width*3
    min-height: 500px; }

  .solution_box {
    text-align: center;
    width: grid_sz(4,3);
    cursor: pointer;
    background-repeat: no-repeat;
    @include trans;
    @include range_width(0px,800px) {
      margin-bottom: grid_sz(0,1);
      width: 100%; }

    &:nth-child(2n) {
      margin-left: $gutter_width;
      margin-right: $gutter_width; }

    & .box_inner {
      display: block;
      position: relative;
      @include box_shadow;
      @include trans;
      height: grid_sz(0,19);
      overflow: hidden;
      cursor: pointer;


      .box_info {
        margin-top: grid_sz(0,3); }
      .box_btn {
        margin-bottom: grid_sz(0,3); }

      .box_text {
        @include flex_split("td");
        @include ab_full;
        z-index: 10; }

      &:after,&:before {
        @include pseudo_ab_full; }

      &:after {
        background-color: rgba(0,0,0,0.4);
        // opacity: 0
        @include trans; }

      &:hover,&:focus {
        height: grid_sz(0,24);
        background-color: transparent;
        color: #333;

        .box_info {
          h4.solution_title {
            color: $color_grey_dark;
            &:after {
              border-bottom: solid 2px  $color_grey_dark; } } }
        .btn {
          opacity: 1; }

        &:after {
          opacity: 0; } }

      h4 {
        color: $color_white; }

      h4.solution_title {
        line-height: 2em;
        color: $color_white;
        @include trans;
        font-weight: 400;

        &:after {
          content: "";
          display: block;
          width: grid_sz(1,0);
          margin: auto;
          margin-top: grid_sz(0,2);
          @include trans;
          border-bottom: solid 2px $color_white; } } }




    .btn {
      margin-bottom: $gutter_width*2;
      @include trans;
      opacity: 0;
      cursor: pointer; }

    &:nth-child(1) .box_inner:before {
      background-image: url("../img/homepage/Home8-1.jpg");
      background-position: 60% bottom;
      background-size: 105% auto;
      background-repeat: no-repeat;
      background-color: white; }


    &:nth-child(2) .box_inner:before {
      background-image: url("../img/homepage/Home8-2.jpg");
      background-position: 60% bottom;
      background-size: 105% auto;
      background-repeat: no-repeat;
      background-color: white; }


    &:nth-child(3) .box_inner:before {
      background-image: url("../img/homepage/Home8-3.jpg");
      background-position: 60% bottom;
      background-size: 105% auto;
      background-repeat: no-repeat;
      background-color: white; } }


  .row_else {
    padding: $gutter_width*1;
    padding-bottom: $gutter_width*2; } }


//----------------------------------------------
.detail_footer {
  min-height: initial;
  padding: $gutter_width*3 0px;
  padding-bottom: $gutter_width*2;

  .container {
    align-items: flex-start;
    @include range_width(0px,768px) {
      flex-direction: column; }
    @include range_width(0px,800px) {
      padding-left: grid_sz(0,1);
      padding-right: grid_sz(0,1); } }


  .col_address {
    width: grid_sz(4,3);
    padding-left: grid_sz(0,1);
    p {
      letter-spacing: 2px; } }

  .col_question {
    width: grid_sz(5,4);
    margin-left: grid_sz(0,1);
    margin-right: grid_sz(0,1); }
  .col_corp {
    width: grid_sz(3,2);

    .item {}
    .company_icon {
      width: grid_sz(1,1);
      margin: grid_sz(0.1);
      margin-left: auto;
      margin-right: auto; } }


  .col_address,.col_question,.col_corp {
    color: $color_grey_light; }



  h5 {
    font-size: 20px;
    color: $color_grey_dark;
    height: grid_sz(0,2); }

  //--------

  hr {
    border: none;
    border: solid 1px #ddd;
    margin-top: grid_sz(0,1);
    margin-bottom: grid_sz(0,1); }

  .social_icon {
    width: 20px;
    margin-right: 10px;
    vertical-align: middle;
    display: inline-block;
    font-size: 20px;
    cursor: pointer;
    color: $color_grey_dark;
    @include trans;
    &:hover {
      color: $color_theme; }
    &.fb {
      width: 20px; } }
  .more {
    border-bottom: solid 2px $color_theme;
    padding-bottom: 3px;
    cursor: pointer;
    &:hover {
      color: $color_theme;
      text-decoration: none; } }


  .icon {
    @include size(18px);
    // background-color: darken($color_white,15)
    border-radius: 2px;
    position: relative;
    display: inline-block;
    position: absolute;
    left: -26px;
    top: 5px;
    opacity: 0.7;
    cursor: pointer;
    @include trans;

    &:hover {
      background-color: $color_theme;
      &:before,&:after {
        background-color: $color_white; } }

    &:before,&:after {
      // transform-origin: center center
      @include trans;
      display: block;
      content: "";
      width: 10px;
      height: 2px;
      background-color: $color_theme;

      @include ab_center; }
    &.icon_plus {

      &:before {
        transform: translate(-50%,-50%) rotate(-90deg); }
      &:after {
        transform: translate(-50%,-50%) rotate(180deg); } } }

  .col_address {
    p {
      font-size: 16px; }
    hr {
      border-bottom: solid 1px #aaa; } }
  .question_list {
    position: relative;

    & {
      list-style: none;
      padding-left: $gutter_width*1.5; }

    li {
      margin-bottom: $gutter_width;
      position: relative;

      &.open {
        .answer {
          max-height: 250px;
          margin-top: $gutter_width/2; } } }

    .question {
      color: $color_grey_light;
      font-size: 16px;
      font-weight: 400;
      letter-spacing: 1px;
      cursor: pointer; }
    .answer {
      color: $color_theme;
      font-weight: 400;
      margin-top: 0;
      max-height: 0px;
      overflow: hidden;
      @include trans;

      // max-height: 0px
 }      // overflow: hidden

    input[type=checkbox]:checked+.answer {
      .answer {
        display: block;
        max-height: 50px; } } }

  .company_icon {
    width: 33%;
    display: block;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto; }


  .go_to_topbtn {
    position: fixed;
    background-color: $color_theme;
    @include size(40px,40px);
    cursor: pointer;

    border-radius: 50%;
    bottom: 65px;
    right: 65px;
    opacity: 1;
    @include trans;

    i {
      @include ab_center;
      color: $color_white;
      font-size: 25px; }

    &.at_top {
      opacity: 0; } }


  @include rwd_md {
    .col_address {
      padding-left: 0px; }
    .col_address,.col_question,.col_corp {
      width: 100%;
      height: auto;
      margin: 0;
      margin-bottom: grid_sz(0,2); }
    .go_to_topbtn {
      bottom: grid_sz(0,1);
      right: grid_sz(0,1); } } }

//-------------------------
.fade-enter-active,.fade-leave-active {
  transition: opacity .5s; }

.fade-enter,.fade-leave-to {
  opacity: 0; }
.fade-enter-to,.fade-leave {
  opacity: 1; }

.fade-delay-enter-active {
  transition: opacity .5s .5s; }
.fade-delay-leave-active {
  transition: opacity .5s; }

.fade-delay-enter,.fade-delay-leave-to {
  opacity: 0; }
.fade-delay-enter-to,.fade-delay-leave {
  opacity: 1; }

